<script setup>
import { ref, onMounted, inject } from "vue";
import { useStore } from "vuex";
const store = useStore();
const go_route_name = inject("go_route_name");
const set_show_page = inject("set_show_page");
const User = ref("");
const Pass = ref("");
onMounted(() => {
  User.value = "";
  Pass.value = "";
});

const setStore = () => {
  store.commit("set_email", User.value);
  store.commit("set_id", Pass.value);
  set_show_page();
  setTimeout(() => {
    go_route_name("home");
    console.log("home");
  }, 500);
};
</script>
<template>
  <div class="row m-0 h-50">
    <div class="col-12 col-md-3"></div>
    <div class="col-12 col-md-4 my-5">
      <main class="form-signin w-100 m-auto was-validated">
        <form>
          <h1 class="h3 mb-3 fw-normal">Please sign in</h1>
          <div class="form-floating my-2">
            <input
              type="email"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              autocomplete="off"
              v-model="User"
              required
            />
            <label for="floatingInput">Email address</label>
          </div>
          <div class="form-floating my-2">
            <input
              type="password"
              class="form-control"
              id="floatingPassword"
              placeholder="Password"
              autocomplete="off"
              v-model="Pass"
              required
            />
            <label for="floatingPassword">Password</label>
          </div>

          <button
            class="btn btn-primary w-100 my-3"
            type="button"
            @click="setStore"
          >
            Sign in
          </button>
        </form>
      </main>
    </div>
  </div>
</template>
